<template>
  <form
    class="mt-32 w-full"
    enctype="multipart/form-data"
    @submit.prevent="addProdut"
  >
    <div class="container shadow-2xl pb-20 m-auto w-11/12">
      <div class="w-full lg:w-1/3 lg:mx-auto">
        <input
          class="w-full my-2 shadow-2xl"
          @change="uploadfile"
          type="file"
          name="fileImg"
          ref="fileimg"
        />
      </div>
      <div class="w-full lg:w-1/3 mx-auto">
        <div>
          {{ fileimg.name }}
        </div>
        <input
          class="w-full shadow-2xl my-2"
          v-model="product.title"
          type="text"
          placeholder="title"
        />
      </div>
      <div class="w-full lg:w-1/3 mx-auto">
        <input
          class="w-full shadow-2xl my-2"
          v-model="product.price"
          type="number"
          placeholder="price"
        />
      </div>
      <div class="w-1/3 text-right shadow-2xl mx-auto">
        <select
          class="border-none text-right bg-gray-500 text-white"
          ref="catogres"
        >
          <option value="phone">هواتف</option>
          <option value="close">ملابس</option>
          <option value="pc">اجهزة حواسيب</option>
        </select>
      </div>
      <div class="w-full mt-2 lg:w-32 mx-auto">
        <input
          class="mx-auto w-full rounded-md p-2 bg-emerald-600 text-white"
          type="submit"
          value="add"
        />
      </div>
    </div>
  </form>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
import Product from "../models/product";
export default {
  data: function () {
    return {
      product: new Product("", ""),
      fileimg: "",
    };
  },
  computed: {
    currentUser() {
      return store.state.auth.user;
    },
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push("/login");
    }
  },
  methods: {
    uploadfile() {
      console.log(this.$refs.catogres.value);
      this.fileimg = this.$refs.fileimg.files[0];
    },
    addProdut() {
      const formData = new FormData();
      formData.append("fileImg", this.fileimg);
      formData.append("title", this.product.title);
      formData.append("price", this.product.price);
      formData.append("catogres", this.$refs.catogres.value);
      formData.append("user_id", this.currentUser.id);
      formData.append("user_img", this.currentUser.urlimg);
      axios
        .post("/product", formData)
        .then(() => {
          alert("تم اضافة منتج جديد");
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style></style>
