<template>
  <div>
    <form
      class="
        w-full
        mx-5
        absolute
        left-1/3
        mt-32
        lg:w-1/3 lg:mx-auto
        p-10
        shadow-xl
        rounded-xl
      "
    >
      <img
        :src="
          'https://res.cloudinary.com/dekh1kgki/image/upload/' +
          product.img_url +
          '.png'
        "
        class="w-full urlimg justify-end items-end flex h-56 bg-cover"
      />
      <input
        class="w-full p-2 my-4 hover:border-blue-500"
        type="text"
        name=""
        v-model="product.title"
        id=""
      />
      <br />

      <input
        class="w-full p-2 hover:border-blue-500"
        type="number"
        name=""
        v-model="product.price"
        id=""
      />
      <br />
      <button
        class="
          w-32
          block
          mt-10
          rounded-md
          text-white
          font-font3
          mx-auto
          bg-green-600
          hover:bg-lightBlue-400
          p-2
          text-center
        "
      >
        تأكيد
      </button>
    </form>
  </div>
</template>

<script>
import { ref } from "vue";
import axios from "axios";
export default {
  methods: {
    submitimg() {
      return `../assets/uploads/1631780464627screenshot-(1).png`;
    },
  },
  setup() {
    const product = ref([]);
    function getProducts() {
      axios
        .get(`/product/${this.$route.params.id}`)
        .then((res) => {
          product.value = res.data;
        })
        .catch((err) => console.log(err));
    }

    return {
      getProducts,
      product,
    };
  },
  mounted() {
    this.getProducts();
    this.submitimg();
  },
};
</script>

