<template>
  <tr>
    <!-- <td><img class="w-11/12" :src="product.img_url" alt=""></td> -->
    <td></td>
    <td class="text-white">{{ product.title }}</td>
    <td class="text-white">{{ product.price }}</td>
    <td class="text-white">
      <!-- <input v-model="product.qyt" type="number" @input="totall()"  min="1" max="10"> -->
    </td>
    <td class="text-white">{{ subtotall }}</td>
    <td>
      <button class="text-white p-2" @click="deleteItem(product)">
        <font-awesome-icon
          class="text-red-500 text-4xl"
          :icon="['fas', 'trash-alt']"
        />
      </button>
    </td>
  </tr>
</template>

<script>
import store from "@/store/index.js";
import { computed } from "vue";
export default {
  props: {
    product: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    function deleteItem(product) {
      store.commit("product/removeProduct", product);
      totall();
    }
    const subtotall = computed(() => {
      return props.product.price * props.product.qyt;
    });
    function totall() {
      store.commit("product/calcsubtotall");
      store.commit("product/calcgrandtotall");
      store.commit("product/storedProduct");
    }
    return {
      deleteItem,
      subtotall,
      totall,
    };
  },
};
</script>

