<template>
  <div class="container mt-16 w-full overflow-hidden">
    <div class="user">
      <div class="flex justify-center">
        <img
          class="rounded-full w-32 h-32 shadow-xl"
          :src="
            'https://res.cloudinary.com/dekh1kgki/image/upload/' +
            currentUser.urlimg +
            '.png'
          "
        />
      </div>

      <div
        class="
          w-1/6
          mx-auto
          text-black
          mt-2
          text-xl
          font-font3 font-semibold
          text-center
        "
      >
        {{ currentUser.username }}
      </div>
      <div
        class="w-1/6 mx-auto mt-2 text-xl font-font3 font-semibold text-center"
      >
        <form enctype="multipart/form-data" @submit.prevent="uploadfile">
          <input @change="selectfile" class="-ml-32" type="file" ref="fileii" />

          <input
            type="submit"
            value="chang your photo"
            class="
              rounded-md
              p-2
              bg-emerald-600
              text-white
              mt-6
              text-center
              font-font1
              -ml-6
            "
          />
        </form>

        <div>{{ msg }}</div>
      </div>
      <img class="w-5/6 mt-10 block mx-auto" :src="imageData" />
    </div>
  </div>
</template>

<script>
export default {
  name: "Profile",

  data: function () {
    return {
      msg: null,
      file: "",
      imageData: null,
    };
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push("/login");
    }
  },
  methods: {
    onSelectFile() {
      const input = this.$refs.fileInput;
      const files = input.files;
      if (files && files[0]) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.imageData = e.target.result;
        };
        reader.readAsDataURL(files[0]);
      }
    },
    chooseImage() {
      this.$refs.fileInput.click();
    },

    selectfile() {
      this.file = this.$refs.fileii.files[0];
      console.log(this.file);
      const reader = new FileReader();
      reader.onload = (e) => {
        this.imageData = e.target.result;
      };
      reader.readAsDataURL(this.file);
    },
    uploadfile() {
      const formData = new FormData();
      formData.append("file", this.file);
      formData.append("id", this.currentUser.id);
      this.$store.dispatch("auth/uploadImgProfile", formData).then(
        () => {
          // this.$router.push("/products");
          // this.$router.go();
          this.imageData = null;
          return this.$store.state.auth.user;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response && error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
  },
};
</script>

