import { createStore } from "vuex";

import { auth } from "./auth.module";
import { product } from "./product.module";
export default createStore({
  state: {
    tmenu: true,
  },
  mutations: {
    togleM(state) {
      console.log(state.tmenu);
      state.tmenu = !state.tmenu;
      console.log(state.tmenu);
    },
    closeM(state) {
      console.log(state.tmenu);
      state.tmenu = true;
    },
  },
  modules: {
    auth,
    product,
  },
});
