<template>
  <div>
    <div>
      <h3 class="text-gray-500 mx-auto container underline shadow-md">
        login <font-awesome-icon :icon="['fas', 'sign-in-alt']" />
      </h3>
    </div>
    <div class="flex items-center justify-center m-auto">
      <div class="p-2 w-full lg:w-3/6 mx-auto">
        <form @submit.prevent="signin" class="flex flex-col w-full mx-auto">
          <input
            v-model="user.username"
            type="text"
            placeholder="username"
            class="
              w-full
              hover:bg-blue-200
              my-2
              border-gray-400 border-2
              hover:border-blueGray-600
            "
          />
          <input
            v-model="user.password"
            type="password"
            placeholder="password"
            class="
              w-full
              hover:bg-blue-200
              border-gray-400 border-2
              hover:border-blueGray-600
            "
          />
          <input
            type="submit"
            class="
              bg-green-600
              mt-6
              cursor-pointer
              text-white
              p-2
              w-4/12
              mx-auto
              rounded-lg
            "
          />
        </form>

        <div
          v-if="message"
          class="alert"
          :class="successful ? 'text-green-500' : 'text-red-400'"
        >
          {{ message }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import User from "../models/user";

export default {
  data: () => {
    return {
      user: new User("", "", ""),
      message: "",
      successful: false,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push("/profile");
    }
  },

  methods: {
    signin: function () {
      this.message = "";
      if (this.user.username && this.user.password) {
        this.$store.dispatch("auth/login", this.user).then(
          () => {
            this.$router.push("/profile");
          },
          (error) => {
            this.loading = false;
            this.message =
              (error.response && error.response.data.message) ||
              error.message ||
              error.toString();
          }
        );
      }
    },
  },
};
</script>

<style></style>
