<template>
  <div class="grid-cols-4 mx-auto w-2/3 mt-10 grid calculator">
    <div class="grid display">
      <input class="text-center" type="text" disabled v-model="current" />
    </div>
    <div
      @click="clear"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      C
    </div>
    <div
      @click="sign"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      + / -
    </div>
    <div
      @click="perent"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      %
    </div>
    <div @click="divid" class="op border text-center border-solid border-black">
      ÷
    </div>
    <div
      @click="append('7')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      7
    </div>
    <div
      @click="append('8')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      8
    </div>
    <div
      @click="append('9')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      9
    </div>
    <div @click="times" class="op border text-center border-solid border-black">
      X
    </div>
    <div
      @click="append('6')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      6
    </div>
    <div
      @click="append('5')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      5
    </div>
    <div
      @click="append('4')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      4
    </div>
    <div @click="mins" class="op border text-center border-solid border-black">
      -
    </div>
    <div
      @click="append('3')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      3
    </div>
    <div
      @click="append('2')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      2
    </div>
    <div
      @click="append('1')"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      1
    </div>
    <div @click="add" class="op border text-center border-solid border-black">
      +
    </div>
    <div class="bg-gray-400 border text-center border-solid zero border-black">
      0
    </div>
    <div
      @click="dot"
      class="bg-gray-400 border text-center border-solid border-black"
    >
      .
    </div>
    <div @click="equal" class="op border text-center border-solid border-black">
      =
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      show: true,
      current: "",
      oparator: null,
      prev: null,
      oparatorClick: false,
    };
  },
  methods: {
    clear() {
      this.current = "";
    },
    sign() {
      this.current =
        this.current.charAt(0) === "-"
          ? this.current.slice(1)
          : "-" + this.current;
    },
    perent() {
      this.current = `${parseFloat(this.current) / 100}`;
    },
    append(number) {
      if (this.oparatorClick) {
        this.current = "";
        this.oparatorClick = false;
      }
      this.current = `${this.current}${number}`;
    },
    dot() {
      if (this.current.indexOf(".") === -1) {
        this.append(".");
      }
      this.prev = this.current;
      this.oparatorClick = true;
    },
    add() {
      this.oparator = (a, b) => a + b;

      this.prev = this.current;
      this.oparatorClick = true;
    },
    mins() {
      this.oparator = (a, b) => a - b;

      this.prev = this.current;
      this.oparatorClick = true;
    },
    times() {
      this.oparator = (a, b) => a * b;

      this.prev = this.current;
      this.oparatorClick = true;
    },
    divid() {
      this.oparator = (a, b) => a / b;

      this.prev = this.current;
      this.oparatorClick = true;
    },
    equal() {
      this.current = `${this.oparator(
        parseFloat(this.current),
        parseFloat(this.prev)
      )}`;
      this.prev = null;
    },
  },
};
</script>

<style>
.calculator {
  grid-auto-rows: minmax(50px, auto);
}
.display {
  grid-column: 1/5;
  text-align: center;
  font-size: 20px;
  background-color: #fff;
}
.zero {
  grid-column: 1/3;
}
.op {
  @apply bg-yellow-600 text-white font-bold;
}
</style>
