<template>
  <div class="container w-full relative mx-auto mt-10 px-6">
    <div>
      <h3
        class="
          text-gray-500
          mx-auto
          container
          text-right text-xl
          font-font3
          shadow-md
        "
      >
        منتجات ( {{ Products.length }} )
      </h3>
    </div>
    <div
      v-if="Products.length == 0 || prductNotFound"
      class="relative top-64 text-center text-3xl lg:text-5xl text-gray-400"
    >
      لاتوجد منتجات {{ prductNotFound }} حاليا
    </div>

    <div class="text-right">
      <select
        class="
          border-none
          bg-gray-500
          text-white text-right
          mt-6
          lg:mt-10 lg:ml-10
        "
        ref="filterP"
        @change="filteredProducts"
        name=""
        id=""
      >
        <option value="All">جميع المنتجات</option>
        <option value="phone">هواتف</option>
        <option value="close">ملابس</option>
        <option value="pc">اجهزة حواسيب</option>
      </select>
    </div>
    <div
      class="
        grid
        gap-6
        grid-cols-1
        transition-all
        duration-500
        sm:grid-cols-3
        xl:grid-cols-4
        mt-6
      "
    >
      <Products
        :product="product"
        v-for="(product, index) in Productss"
        :key="index"
      />
    </div>
  </div>
</template>

<script>
import Products from "@/components/Products";
import { ref } from "vue";
import axios from "axios";
export default {
  data() {
    return {
      Productss: [],
      prductNotFound: null,
    };
  },
  components: {
    Products,
  },
  methods: {
    filteredProducts: function () {
      this.Productss = [];

      if (
        this.Products.filter((product) => {
          if (product.catogres == this.$refs.filterP.value) {
            return this.Productss.push(product);
          }
        }).length > 0
      ) {
        return;
      } else if (this.$refs.filterP.value === "All") {
        this.Productss = this.Products;
      } else {
        this.Productss = [];
        if (this.$refs.filterP.value === "pc") {
          this.prductNotFound = "اجهزة حواسيب";
        } else if (this.$refs.filterP.value === "phone") {
          this.prductNotFound = "هواتف";
        } else if (this.$refs.filterP.value === "close") {
          this.prductNotFound = "ملابس";
        }
      }
    },
  },
  setup() {
    const Products = ref([]);
    function getProducts() {
      axios
        .get("/product")
        .then((res) => {
          Products.value = res.data;
          this.Productss = res.data;
        })
        .catch((err) => console.log(err));
    }
    return {
      Products,
      getProducts,
    };
  },

  mounted() {
    this.getProducts();
  },
};
</script>
<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: all 1s;
}
.fade-enter, .fade-leave-active /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
