import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import Cart from "../views/Cart.vue";
import signup from "../views/signup.vue";
import signin from "../views/signin.vue";
import profile from "../views/profile.vue";
import products from "../views/products.vue";
import userborad from "../views/userborad.vue";
import Adminborad from "../views/Adminborad.vue";
import modborad from "../views/modborad.vue";
import seed from "../views/seed.vue";
import seed2 from "../views/seed2.vue";
import addproduct from "../views/addproduct.vue";
import product from "../views/product.vue";
import eaditProduct from "../views/eaditProduct.vue";
import hostfinder from "../views/hostfinder.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/cart",
    name: "cart",
    component: Cart,
  },
  {
    path: "/signup",
    name: "signup",
    component: signup,
  },
  {
    path: "/signin",
    name: "signin",
    component: signin,
  },
  {
    path: "/profile",
    name: "profile",
    component: profile,
  },
  {
    path: "/products",
    name: "products",
    component: products,
  },
  {
    path: "/mod",
    name: "mod",
    component: modborad,
  },
  {
    path: "/user",
    name: "user",
    component: userborad,
  },
  {
    path: "/admin",
    name: "admin",
    component: Adminborad,
  },
  {
    path: "/seed",
    name: "seed",
    component: seed,
  },
  {
    path: "/seed2",
    name: "seed2",
    component: seed2,
  },
  {
    path: "/addproduct",
    name: "addproduct",
    component: addproduct,
  },
  {
    path: "/product/:id",
    name: "product",
    component: product,
  },
  {
    path: "/eaditProduct/:id",
    name: "eaditProduct",
    component: eaditProduct,
  },
  {
    path: "/hostfinder",
    name: "hostfinder",
    component: hostfinder,
  },
  // {
  //   path: "/about",
  //   name: "About",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "about" */ "../views/About.vue"),
  // },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
