<template>
  <div
    class="
      w-full
      max-w-sm
      mx-auto
      lg:absolute
      mt-32
      left-1/3
      p-2
      rounded-md
      shadow-2xl
    "
  >
    <div
      :style="{
        'background-image':
          'url(https://res.cloudinary.com/dekh1kgki/image/upload/' +
          product.img_url +
          '.png)',
      }"
      class="w-full urlimg justify-end items-end flex h-56 bg-cover"
    ></div>

    <button
      class="rounded-full block mx-auto mt-3 p-2"
      :class="isAdded ? 'bg-red-600 ' : 'bg-green-600 '"
      @click="addtoCart(product)"
    >
      <font-awesome-icon
        class="text-white text-3xl"
        v-if="!isAdded"
        :icon="['fas', 'cart-plus']"
      />
      <font-awesome-icon
        class="text-white text-3xl"
        v-else
        :icon="['fas', 'trash-alt']"
      />
    </button>

    <div class="py-3 px-3">
      <h3 class="text-center bold bg-gray-500 text-white p-2">
        {{ product.title }}
      </h3>
      <p class="rounded-full bold text-md mt-2 text-right">
        السعر : ${{ product.price }}
      </p>
    </div>
    <div v-if="currentUser.id == product.user_id">
      <button
        class="rounded-md p-2 bg-red-500 text-white"
        @click="deleteproduct"
      >
        حزف
      </button>

      <router-link
        :to="'/eaditProduct/' + product._id"
        class="rounded-md p-2 mb-20b bg-emerald-600 text-white ml-4"
      >
        تعديل
      </router-link>
    </div>
  </div>
  <br />
  <br />
</template>

<script>
import { ref, computed, reactive } from "vue";
import axios from "axios";
import store from "../store/index";
export default {
  methods: {
    deleteproduct() {
      axios
        .delete(`/product/${this.product._id}`)
        .then(() => {
          store.commit("product/removeProduct", this.product);
          store.commit("product/storedProduct");
          this.$router.push("/products");
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  setup() {
    const product = ref([]);
    function getProducts() {
      axios
        .get(`/product/${this.$route.params.id}`)
        .then((res) => {
          product.value = res.data;
        })
        .catch((err) => console.log(err));
    }
    const isAdded = ref(false);
    const cart = computed(() => {
      return store.state.product.cart;
    });
    function addtoCart(product) {
      console.log(this);
      isAdded.value = !isAdded.value;
      if (isAdded.value) {
        store.commit("product/addtoCart", product);
        store.commit("product/storedProduct");
      } else {
        store.commit("product/removeProduct", product);
        store.commit("product/storedProduct");
      }
    }
    function checkAdedd() {
      const check = cart.value.some((el) => {
        return el._id == product.value._id;
      });
      if (check) {
        isAdded.value = true;
      }
    }
    // function inlineBgImage() {
    //   let bgImage = require("../../public/1632475934172screenshot-(2).png");

    //   return bgImage;
    // }
    const lazyOptions = reactive({
      src: "1632475934172screenshot-(2).png",
      lifecycle: {
        loading: () => {
          console.log("image loading");
        },
        error: () => {
          console.log("image error");
        },
        loaded: () => {
          console.log("image loaded");
        },
      },
    });
    return {
      lazyOptions,
      product,
      getProducts,
      cart,
      addtoCart,
      isAdded,
      checkAdedd,
    };
  },
  beforeUpdate() {
    this.checkAdedd();
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
  mounted() {
    this.getProducts();
  },
};
</script>
