<template>
  <div>
    <div class="mt-16">
      <h3 class="text-gray-500 mx-auto container underline shadow-md">
        add new user <font-awesome-icon :icon="['fas', 'user-plus']" />
      </h3>
    </div>
    <div class="flex items-center justify-center m-auto">
      <div class="p-2 w-full lg:w-3/6 mx-auto">
        <form @submit.prevent="signup" class="flex flex-col w-full mx-auto">
          <input
            v-model="user.username"
            type="text"
            placeholder="اسم المستخدم"
            class="
              w-full
              hover:bg-blue-200
              my-2
              border-gray-400 border-2
              hover:border-blueGray-600
            "
          />
          <input
            v-model="user.password"
            type="password"
            placeholder="كلمة السر"
            ref="password"
            class="
              w-full
              hover:bg-blue-200
              border-gray-400 border-2
              hover:border-blueGray-600
            "
          />
          <input
            v-model="user.repassword"
            type="password"
            ref="repassword"
            placeholder="اعد كتابة كلمة السر "
            class="
              w-full
              mt-3
              hover:bg-blue-200
              border-gray-400 border-2
              hover:border-blueGray-600
            "
          />
          <input
            v-model="user.email"
            type="text"
            placeholder="الايميل"
            class="
              w-full
              hover:bg-blue-200
              my-2
              border-gray-400 border-2
              hover:border-blueGray-600
            "
          />
          <div class="text-right">
            <router-link to="/signin" class="text-blue-500">
              قم بالضغط هنا اذا كان لديك حساب مسبقا
            </router-link>
          </div>
          <input
            type="submit"
            class="
              bg-green-600
              mt-6
              cursor-pointer
              text-white
              p-2
              w-4/12
              mx-auto
              rounded-lg
            "
            value="تسجيل جديد"
          />
        </form>

        <div
          v-if="message"
          class="alert"
          :class="successful ? 'text-green-500' : 'text-red-400'"
        >
          {{ message }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import User from "../models/user";
export default {
  data: () => {
    return {
      user: new User("", "", ""),
      message: "",
      successful: false,
      wrongPassword: null,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push("/profile");
    }
  },

  methods: {
    signup: function () {
      this.message = "";
      console.log(this.$refs.password.value);
      console.log(this.$refs.repassword.value);
      if (this.$refs.password.value === this.$refs.repassword.value) {
        this.$store.dispatch("auth/register", this.user).then(
          (data) => {
            this.message = data.message;
            this.successful = true;
            alert("تم التسجيل بنجاح");
            this.$router.push("/signin");
          },
          (error) => {
            this.successful = false;
            this.message =
              (error.response && error.response.data.message) ||
              error.message ||
              error.message.toString();
          }
        );
      } else {
        this.message = "كلمة السر غير مطابقتين";
      }
    },
  },
};
</script>

<style scoped>
input::placeholder {
  text-align: right;
}
</style>
