export const product = {
  namespaced: true,
  state: {
    cart: [],
    subtotall: null,
    grandtotall: null,
    tax: null,
  },
  mutations: {
    addtoCart(state, payload) {
      state.cart.unshift(payload);
    },
    removeProduct(state, payload) {
      const index = state.cart.indexOf(payload);
      state.cart.splice(index, 1);
    },
    storedProduct(state) {
      const parsed = JSON.stringify(state.cart);
      localStorage.setItem("cart", parsed);
    },
    getStoredProduct(state) {
      if (localStorage.getItem("cart")) {
        try {
          state.cart = JSON.parse(localStorage.getItem("cart"));
        } catch (err) {
          localStorage.removeItem("cart");
        }
      }
    },
    calcsubtotall(state) {
      if (state.cart.length == 1) {
        state.cart.forEach((product) => {
          state.subtotall = product.price * product.qyt;
        });
      } else if (state.cart.length > 1) {
        var totall = state.cart.reduce((a, b) => {
          return a + b.price * b.qyt;
        }, 0);
        state.subtotall = totall;
      }
    },
    calcgrandtotall(state) {
      var tax = (state.subtotall * 12) / 100;
      state.grandtotall = state.subtotall + tax;
      state.tax = tax;
    },
  },
};
