import axios from 'axios';

const API_URL = 'https://cryptic-depths-10962.herokuapp.com/api/user/';
// const API_URL = 'http://localhost:8081/api/user/';

class AuthService {
  login(user) {
    return axios
      .post(API_URL + 'signin', {
        username: user.username,
        password: user.password
      })
      .then(response => {
        if (response.data.accessToken) {
          localStorage.setItem('user', JSON.stringify(response.data));
        }
        return response.data;
      })
    }
    
  logout() {
    localStorage.removeItem('user');
  }
  uploadImgProfile(user){
    return axios.post(API_URL + 'stats' , user ).then(response => {
      if (response.data.accessToken) {
        // localStorage.removeItem('user')
        localStorage.setItem('user', JSON.stringify(response.data));
        
      }
  return response.data;
});
  }

  register(user) {
    return axios.post(API_URL + 'singup', {
      username: user.username,
      email: user.email,
      password: user.password
    });
  }
}

export default new AuthService();