<template>
  <div
    style="background-color: white"
    class="
      w-full
      py-4
      max-w-sm
      transition-all
      duration-500
      mx-auto
      rounded-md
      shadow-2xl
      overflow-hidden
    "
  >
    <div
      :style="{
        'background-image':
          'url(https://res.cloudinary.com/dekh1kgki/image/upload/' +
          product.img_url +
          '.png)',
      }"
      class="w-full h-64 bg-center bg-contain bg-no-repeat"
    ></div>

    <button
      class="rounded-full mt-6 mx-auto block p-2"
      :class="
        isAdded
          ? 'bg-red-600 hover:bg-red-500 focus:bg-red-500'
          : 'bg-green-600 hover:bg-green-500 focus:bg-green-500'
      "
      @click="addtoCart(product)"
    >
      <font-awesome-icon
        class="text-white text-3xl"
        v-if="!isAdded"
        :icon="['fas', 'cart-plus']"
      />
      <font-awesome-icon
        class="text-white text-3xl"
        v-else
        :icon="['fas', 'trash-alt']"
      />
    </button>

    <div class="py-3 px-3">
      <h3 class="text-center text-white">{{ product.title }}</h3>
      <p
        class="rounded-md text-white mx-auto text-center w-16 h-6 bg-green-500"
      >
        {{ product.price }}$
      </p>

      <button class="text-center block mx-auto">
        <router-link
          class="p-2 text-green-700 hover:text-green-500 rounded-lg"
          :to="'/product/' + product._id"
          >عرض المزيد</router-link
        >
      </button>
    </div>
  </div>
</template>

<script>
import { ref, computed } from "vue";

import store from "@/store/index.js";
export default {
  props: {
    product: {
      type: Object,
      require: true,
    },
  },
  methods: {},
  setup(props) {
    const isAdded = ref(false);
    const cart = computed(() => {
      return store.state.product.cart;
    });
    function addtoCart(product) {
      isAdded.value = !isAdded.value;
      if (isAdded.value) {
        store.commit("product/addtoCart", product);
        store.commit("product/storedProduct");
      } else {
        store.commit("product/removeProduct", product);
        store.commit("product/storedProduct");
      }
    }
    function checkAdedd() {
      var product = props.product._id;
      var check = cart.value.some((el) => {
        return el._id == product;
      });
      if (check) {
        isAdded.value = true;
      }
    }

    return {
      cart,
      addtoCart,
      isAdded,
      checkAdedd,
    };
  },
  mounted() {
    this.checkAdedd();
  },
};
</script>
