<template>
  <div class="mt-10 absolute w-full">
    <div v-if="cart.length > 0">
      <div>
        <h3 class="text-gray-500 mx-auto container underline shadow-md">
          cart <font-awesome-icon :icon="['fas', 'user-plus']" />
        </h3>
      </div>

      <div class="flex flex-col container m-auto">
        <div class="flex flex-col lg:flex-row w-full">
          <div class="flex lg:w-3/5 w-full mt-4">
            <table class="table-auto m-auto w-11/12">
              <thead>
                <tr class="h-12 border-b border-solid border-gray-400">
                  <td></td>
                  <td class="text-green-600">product</td>
                  <td class="text-green-600">price</td>
                  <td class="text-green-600">Quntity</td>
                  <td class="text-green-600">subtotall</td>
                  <td class="text-green-600">delete</td>
                </tr>
              </thead>
              <tbody>
                <productCart
                  :product="product"
                  v-for="(product, index) in cart"
                  :key="index"
                />
              </tbody>
            </table>
          </div>
          <div class="md:w-7/12 lg:2/5 w-full shadow-lg p-4 m-auto mt-6">
            <table>
              <tr class="text-center text-2xl">
                <td>subtotall</td>
                <td>{{ subtotall }}</td>
              </tr>
              <tr class="text-center text-2xl">
                <td>tax</td>
                <td>{{ tax }}</td>
              </tr>
              <tr class="text-center text-3xl">
                <td class="text-white">totall</td>
                <td class="text-white">
                  {{ Number.parseFloat(grandtotall).toFixed(2) }}
                </td>
              </tr>
            </table>
            <div>
              <a href="#">
                <button
                  class="
                    bg-green-600
                    text-2xl text-white
                    p-2
                    rounded-md
                    font-font3
                    text-center
                  "
                >
                  proceed to checkout
                </button>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div
      v-if="cart.length <= 0"
      class="
        flex
        justify-center
        h-64
        items-center
        font-font2
        opacity-50
        text-5xl
        font-bold
      "
    >
      <h1>the cart is empty</h1>
    </div>
  </div>
</template>

<script>
import { computed } from "vue";
import store from "@/store/index.js";
import productCart from "@/components/productCart.vue";
export default {
  components: {
    productCart,
  },
  data: function () {
    return {};
  },
  setup() {
    const cart = computed(() => {
      return store.state.product.cart;
    });
    const tax = computed(() => {
      return store.state.product.tax;
    });
    const grandtotall = computed(() => {
      return store.state.product.grandtotall;
    });
    const subtotall = computed(() => {
      return store.state.product.subtotall;
    });
    return {
      cart,
      subtotall,
      tax,
      grandtotall,
    };
  },
  mounted() {
    store.commit("product/calcsubtotall");
    store.commit("product/calcgrandtotall");
  },
};
</script>

