<template>
  <div>
    <div class="mt-10">
      <div
        v-for="product in products"
        :key="product"
        class="mt-4 mx-2 flex lg:w-2/3 mx-auto shadow-xl"
      >
        <div
          v-if="currentUser.id == product.user_id"
          class="py-3 lg:w-1/3 flex justify-between lg:px-3"
        >
          <h3 class="text-center text-gray-900 ml-2 mr-2">
            {{ product.title }}
          </h3>
          <p class="text-gray-800">: {{ product.price }} $</p>
          <input type="text" hidden ref="productid" v-model="product._id" />
        </div>

        <div v-if="currentUser.id == product.user_id">
          <button
            :title="product._id"
            class="rounded-md p-2 ml-10 bg-red-500 text-white"
            @click="deleteproduct(product._id)"
          >
            delete
          </button>

          <router-link
            :to="'/eaditProduct/' + product._id"
            class="rounded-md p-2 bg-emerald-600 text-white ml-4"
          >
            modified
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from "vue";
import store from "@/store/index.js";
import axios from "axios";
export default {
  methods: {
    deleteproduct(event) {
      console.log(event);
      axios
        .delete(`/product/${event}`)
        .then(() => {
          store.commit("product/removeProduct", this.product);
          store.commit("product/storedProduct");
          this.getProducts();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  setup() {
    const products = ref([]);
    function getProducts() {
      axios
        .get(`/product`)
        .then((res) => {
          products.value = res.data;
        })
        .catch((err) => console.log(err));
    }
    const isAdded = ref(false);
    const cart = computed(() => {
      return store.state.product.cart;
    });
    function addtoCart(product) {
      console.log(this);
      isAdded.value = !isAdded.value;
      if (isAdded.value) {
        store.commit("product/addtoCart", product);
        store.commit("product/storedProduct");
      } else {
        store.commit("product/removeProduct", product);
        store.commit("product/storedProduct");
      }
    }
    function checkAdedd() {
      const check = cart.value.some((el) => {
        return el._id == products.value._id;
      });
      if (check) {
        isAdded.value = true;
      }
    }
    // function inlineBgImage() {
    //   let bgImage = require("../../public/1632475934172screenshot-(2).png");

    //   return bgImage;
    // }
    // const lazyOptions = reactive({
    //   src: "1632475934172screenshot-(2).png",
    //   lifecycle: {
    //     loading: () => {
    //       console.log("image loading");
    //     },
    //     error: () => {
    //       console.log("image error");
    //     },
    //     loaded: () => {
    //       console.log("image loaded");
    //     },
    //   },
    // });
    return {
      products,
      getProducts,
      cart,
      addtoCart,
      isAdded,
      checkAdedd,
    };
  },
  beforeUpdate() {
    this.checkAdedd();
  },
  computed: {
    currentUser() {
      return store.state.auth.user;
    },
  },
  mounted() {
    if (!this.currentUser.roles.includes("ROLE_MODERATOR")) {
      this.$router.push("/profile");
    }
    this.getProducts();
  },
};
</script>

